<template>
    <div class="section">
        <Head :title="splListTitleName" showRight="true" showLeft="true"></Head>
        <h2>{{post.title}}</h2>
        <div class="post">
            <p class="user clearfix">
                <span class="nick">{{post.nick}}</span>
                <span class="host">楼主</span>
                <span class="reviewIcon">
                    <i class="iconfont" v-html="'&#xe761;'"></i>
                    {{post.review}}
                </span>
                <span class="hot">
                    <i class="iconfont" v-html="'&#xe655;'"></i>
                    {{post.hot}}
                </span>
            </p>
            <p class="date clearfix">
                <span class="time">{{date}}</span>
                <span class="report">举报</span>
            </p>
            <p class="content">
                {{post.brief}}
            </p>
        </div>
        <mt-loadmore :top-method="loadTop" ref="loadmore">
        <div class="review" v-for="(r,index) in review" :key="index">
            <p class="user clearfix">
                <span class="nick">{{r.nick}}</span>
                <span class="reviewIcon">
                    <i class="iconfont" v-html="'&#xe761;'"></i>
                    {{r.reviewCount}}
                </span>
                <span class="hot">
                    <i class="iconfont" v-html="'&#xe6de;'"></i>
                    {{r.hot}}
                </span>
            </p>
            <p class="date clearfix">
                <span class="time">{{timestampToTime(r.date)}}</span>
                <span class="report">举报</span>
            </p>
            <p class="content">
                {{r.reviewMsg}}
            </p>
        </div>
    </mt-loadmore>

        <div class="reviewInput">
            <input type="text" placeholder="回复楼主" v-model="reviewMsg">
            <button class="btn" :disabled="username? false:true" @click="setReview">回复</button>
        </div>
    </div>
</template>


<script>
import store from "../vuex/store";
import axios from "axios";
import { Toast } from "mint-ui"
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    computed:{
        ...mapState(['post','reviewMsg','review']),
        post:{
            get(){
                return this.$store.state.post
            }
        },
        review:{
            get(){
                return this.$store.state.review
            }
        },
        reviewMsg:{
            get(){
               return this.$store.state.reviewMsg 
            },
            set(newMsg){
                this.changeReviewMsg(newMsg)
            }
        },
        date:{
            get(){
                var time = this.$store.state.post.times*1000
                var date = new Date(time);
                console.log(time,date)
                var year = date.getFullYear();
                var month = date.getMonth()+1;
                var day = date.getDate();
                var hour = date.getHours();
                var min = date.getMinutes();
                return year+'-'+month+'-'+day+' '+hour+':'+min
            }
        },
        username:{
             get(){
                 return localStorage.username
             }
         }
    },
    methods:{
        ...mapActions(['getPost','getReview']),
        ...mapMutations(['changeReviewMsg']),
        setReview(){
            var nick = localStorage.nickname;
            if(this.reviewMsg != ""){
                 axios.post('http://118.31.74.14:3080/vue/review',
                    {
                        postId:this.post.id+'',
                        postReview:this.post.review*1+1,
                        nick,
                        reviewMsg:this.reviewMsg,
                        date:new Date().getTime()/1000,
                        hot:'0',
                        reviewCount:'0'
                    }
                ).then(res=>{
                    console.log(res.data);
                    Toast(res.data);
                    this.reviewMsg = ""
                    this.getReview({url:'vue/getReview',id:this.post.id+''})
                })
            }
        },
        timestampToTime(timestamp) {
            var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
            var Y = date.getFullYear() + '-';
            var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
            var D = date.getDate() + ' ';
            var h = date.getHours() + ':';
            var m = date.getMinutes() + ':';
            var s = date.getSeconds();
            return Y+M+D+h+m+s;
        },
        loadTop(){
            console.log('下拉刷新')
            setTimeout(()=>{
                Toast('下拉刷新数据成功!');
                this.getReview({url:'vue/getReview',id:this.post.id+''})
                    this.$refs.loadmore.onTopLoaded();
            },1500)
        },
    },
    created(){
        var url = window.location.href.split("?")[1]
        var id = decodeURI(url.split('=')[1])
        this.getPost({url:'vue/post',id});
        this.getReview({url:'vue/getReview',id})
    },
}
</script>






<style lang="scss" scoped>
    .section{
        padding: 80px 0;
    }
    h2{
        width: 100%;
        font-size: 36px;
        color: rgb(240, 56, 32);
        line-height: 50px;
        padding: 20px 30px;
        box-sizing: border-box;
        border-bottom: 2px solid #ccc;
        background-color: #fff;
    }
    .post{
        width: 100%;
        background-color: #fff;
        padding:20px 30px;
        border-top: 2px solid #ccc;
        box-sizing: border-box;
        .user{
            width: 100%;
            line-height: 48px;
            .nick{
                font-size: 26px;
                color:#a0a0a0;
                float: left;
            }
            .host{
                font-size: 20px;
                width: 60px;
                height: 40px;
                float: left;
                border:2px solid rgb(240, 56, 32);
                color:rgb(240, 56, 32);
                text-align: center;
                line-height: 40px;
                border-radius: 5px;
                margin-left: 20px;
            }
            .hot{
                float:right;
                font-size: 26px;
                color:#a0a0a0;
                margin-right: 30px;
            }
            .reviewIcon{
                float:right;
                font-size: 26px;
                color:#a0a0a0;
            }
        }
        .date{
            width: 100%;
            font-size: 20px;
            .time{
                float: left;
            }
            .report{
                float:right;
                color:blue;
            }
        }
        .content{
            line-height: 60px;
            font-size: 32px;
        }
    }
    .review{
    width: 100%;
    background-color: #fff;
    padding:20px 30px;
    border-top: 2px solid #ccc;
    border-bottom: 2px solid #ccc;
    margin: 15px 0;
    box-sizing: border-box;
    .user{
        width: 100%;
        line-height: 48px;
        .nick{
            font-size: 26px;
            color:#a0a0a0;
            float: left;
        }
        .hot{
            float:right;
            font-size: 26px;
            color:#a0a0a0;
            margin-right: 30px;
        }
        .reviewIcon{
            float:right;
            font-size: 26px;
            color:#a0a0a0;
        }
    }
    .date{
        width: 100%;
        font-size: 20px;
        .time{
            float: left;
        }
        .report{
            float:right;
            color:blue;
        }
    }
    .content{
        line-height: 60px;
        font-size: 32px;
    }
}

.reviewInput{
    width: 100%;
    height: 80px;
    padding: 10px 30px;
    position: fixed;
    left: 0;
    bottom: 0;
    box-sizing: border-box;
    background-color: #fff;
    border-top: 2px solid #bbb;
    input{
        width: 80%;
        height: 100%;
        border: 2px solid #ddd;
    }
    .btn{
        width: 16%;
        height: 100%;
        background-color: rgb(93, 199, 61);
        border:0;
        border-radius: 4px;
    }
}
    
</style>




