<template>
    <div class="section">
        <Head :title="'手机号注册'" showLeft="true"></Head>
        <div class="tel">
            <i v-html="'&#xe605;'" class="iconfont"></i>
            <input type="text" placeholder="请输入11位手机号码" v-model="username">
        </div>
        <p class="usernamemsg">{{usernamemsg}}</p>
        <div class="nickName">
            <i v-html="'&#xe796;'" class="iconfont"></i>
            <input type="text" placeholder="请填写昵称" v-model="nickname">
        </div>
        <p class="nicknamemsg">{{nicknamemsg}}</p>
        <div class="authCode">
            <div class="left">
            <i v-html="'&#xe600;'" class="iconfont"></i>
            <input type="text" placeholder="请填写手机验证码" v-model="authCode">
            </div>
            <div class="right">
            <button id='send' class="send" @click="sendAuthCode">发送验证码</button>
            </div>
        </div>
        <p class="nicknamemsg">{{authCodeMsg}}</p>
        <div class="pwd">
            <i v-html="'&#xe802;'" class="iconfont"></i>
            <input type="password" placeholder="请输入4位数以上登录密码"  v-model="userpwd">
        </div>
        <p class="userpwdmsg">{{userpwdmsg}}</p>
        <div class="pwd2">
            <i v-html="'&#xe802;'" class="iconfont"></i>
            <input type="password" placeholder="请输入4位数以上登录密码"  v-model="userpwd2">
        </div>
        <p class="userpwd2msg">{{userpwd2msg}}</p>
        <mt-button type="primary" size="large" :disabled = "registerbtnFlag" @click="registerIn()">注册</mt-button>
        <p>
        <input type="checkbox" id="msg" v-model="check">
        <label for="msg">我已阅读并同意</label>
        <router-link :to="{name:''}">
            <span>用户协议和隐私政策</span>
        </router-link>
        </p>
    </div>
</template>

<script>
import store from "../vuex/store";
import axios from 'axios';
import md5 from 'js-md5';
import { Toast } from 'mint-ui';
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    computed:{
        ...mapState(['username','userpwd','userpwd2','nickname','usernamemsg','userpwdmsg','userpwd2msg','nicknamemsg','registerbtnFlag','registerFlag','check','gotologin','authCode','msgCode','authCodeMsg']),
        username:{
            get(){
                return this.$store.state.username;
            },
            set(newval){
                // this.$store.commit("updatePhone",newval)
                this.updateUsername(newval)
            }
        },
        userpwd:{
            get(){
                return this.$store.state.userpwd;
            },
            set(newval){
                this.updateUserpwd(newval)
            }
        },
        authCode:{
            get(){
                return this.$store.state.authCode;
            },
            set(newval){
                this.updateAuthCode(newval)
            }
        },
        userpwd2:{
            get(){
                return this.$store.state.userpwd2;
            },
            set(newval){
                this.updateUserpwd2(newval)
            }
        },
        nickname:{
            get(){
                return this.$store.state.nickname;
            },
            set(newval){
                // this.$store.commit("updatePhone",newval)
                this.updateNickname(newval)
            }
        },
        check:{
            get(){
                return this.$store.state.check;
            },
            set(newval){
                // this.$store.commit("updatePhone",newval)
                this.updateCheck(newval)
            }
        }
        
    },
    methods:{
        ...mapMutations({
            updateUsername:"getUsername",
            updateUserpwd:"getUserpwd",
            updateUserpwd2:"getUserpwd2",
            updateNickname:"getNickname",
            updateCheck:'getCheck',
            updateAuthCode:'getAuthCode',
            setMsgCode:'setMsgCode'
        }),
        setMsgCode(e){
            this.$store.commit("setMsgCode",e);
        },
        getUsername(e){
            console.log(e);
            this.$store.commit("getUsername",e);
        },
        getUserpwd(e){
            console.log(e);
            this.$store.commit("getUserpwd",e);
        },
        getAuthCode(e){
            console.log(e);
            this.$store.commit("getAuthCode",e);
        },
        getUserpwd2(e){
            console.log(e);
            this.$store.commit("getUserpwd",e);
        },
        getNickname(e){
            console.log(e);
            this.$store.commit("getNickname",e);
        },
        getCheck(e){
            this.$store.commit("getCheck",e);
        },
        ...mapActions([
            'register',
        ]),
        registerIn(){
            this.register({url:'vue/register',username:this.username,tel:this.username,userpwd:this.userpwd,nickname:this.nickname,callback:this.show})
        },
        show(){
            if(this.registerFlag["code"] == 2){
            Toast({
                message: this.registerFlag.msg,
                iconClass: 'icon icon-success'
            })
            localStorage.username = this.username
            this.$router.push('/login')
            }else{
            Toast({
                message: this.registerFlag.msg
            })
            }
        },
        sendAuthCode(){
            var btn = document.getElementById('send')
            var count = 20;
            btn.disabled = true

            var phone = this.username;
            var timestamp = this.getTimestamp()
            axios.get('vue/sendMsg',{
                params:{
                phone,
                timestamp
            }}).then((res)=>{
                console.log(res.data.code)
                if(res.data.code == 2){
                    Toast(res.data.msg);
                    this.setMsgCode(res.data.authCode)
                }else{
                    Toast(res.data.msg);
                }
            })
            
            var timer = null
            timer = setInterval(()=>{
                btn.innerHTML = count+'秒后可再次点击';
                count--;
                if(count<0){
                    clearInterval(timer);
                    btn.disabled = false;
                    btn.innerHTML = '发送验证码'
                }
            },1000)

        },
        getTimestamp(){
                var date = new Date();
                var year = date.getFullYear();
                var month = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1)
                var day = (date.getDate() < 10 ? '0'+(date.getDate()) : date.getDate())
                var hour = date.getHours();
                var min = date.getMinutes();
                var sec = date.getSeconds();
                return year+''+month+day+hour+min+sec
            }
    }
}
</script>




<style lang="scss" scoped>
    .section{
    padding-top: 80px;
    }
    .tel{
        width: 96%;
        margin: 10px auto;
        height: 100px;
        border:1px solid #ccc;
        box-sizing: border-box;
        padding: 20px 25px;
        background-color: #fff;
        i{
            display: inline-block;
            width: 60px;
            height: 60px;
            vertical-align: middle;
            line-height: 60px;
            font-size: 45px;
        }
        input{
            width: 500px;
            height: 60px;
            vertical-align: middle;
            font-size: 30px;
            line-height: 60px;
            border:0;
        }
    }
    .authCode{
        width: 96%;
        margin: 10px auto;
        height: 100px;      
        .left{
            float: left;
            width: 55%;
            border:1px solid #ccc;
            padding: 20px 25px;
            background-color: #fff;
            box-sizing: border-box;  
            i{
                display: inline-block;
                width: 60px;
                height: 60px;
                vertical-align: middle;
                line-height: 60px;
                font-size: 45px;
            }
            input{
                width: 240px;
                height: 60px;
                vertical-align: middle;
                font-size: 30px;
                line-height: 60px;
                border:0;
            }
        }
        .right{
            width: 42%;
            float: right;
            .send{
                display: block;
                width: 100%;
                height: 100px;
                border:0;
                background-color: green;
                color:white;
            }
        }
    }
    .pwd{
        width: 96%;
        margin: 10px auto;
        height: 100px;
        border:1px solid #ccc;
        box-sizing: border-box;
        padding: 20px 25px;
        background-color: #fff;
        i{
            display: inline-block;
            width: 60px;
            height: 60px;
            vertical-align: middle;
            line-height: 60px;
            font-size: 45px;
        }
        input{
            width: 500px;
            height: 60px;
            vertical-align: middle;
            font-size: 30px;
            line-height: 60px;
            border:0;
        }
    }
    .pwd2{
        width: 96%;
        margin: 10px auto;
        height: 100px;
        border:1px solid #ccc;
        box-sizing: border-box;
        padding: 20px 25px;
        background-color: #fff;
        i{
            display: inline-block;
            width: 60px;
            height: 60px;
            vertical-align: middle;
            line-height: 60px;
            font-size: 45px;
        }
        input{
            width: 500px;
            height: 60px;
            vertical-align: middle;
            font-size: 30px;
            line-height: 60px;
            border:0;
        }
    }
    .nickName{
        width: 96%;
        margin: 10px auto;
        height: 100px;
        border:1px solid #ccc;
        box-sizing: border-box;
        padding: 20px 25px;
        background-color: #fff;
        i{
            display: inline-block;
            width: 60px;
            height: 60px;
            vertical-align: middle;
            line-height: 60px;
            font-size: 45px;
        }
        input{
            width: 500px;
            height: 60px;
            vertical-align: middle;
            font-size: 30px;
            line-height: 60px;
            border:0;
        }
    }
    .mint-button--large{
        width: 96%;
        margin: 0 auto;
        margin-top: 20px;
    }
    p{
        height: 80px;
        margin-top: 20px;
        line-height: 80px;
        font-size: 30px;
        padding-left: 20px;
        span{
            width: 50%;
            text-align: center;
        }
    }
     .usernamemsg{
        width: 100%;
        height: 30px;
        box-sizing: border-box;
        line-height: 30px;
        padding:0 20px;
        color:red;
    }
     .userpwdmsg{
        width: 100%;
        height: 30px;
        box-sizing: border-box;
        line-height: 30px;
        padding:0 20px;
        color:red;
    }
     .userpwd2msg{
        width: 100%;
        height: 30px;
        box-sizing: border-box;
        line-height: 30px;
        padding:0 20px;
        color:red;
    }
     .nicknamemsg{
        width: 100%;
        height: 30px;
        box-sizing: border-box;
        line-height: 30px;
        padding:0 20px;
        color:red;
    }
</style>


