<template>
    <div class="section">
        <Head :title="'手机号登录'" showLeft="true"></Head>
        <div class="tel">
            <i v-html="'&#xe605;'" class="iconfont"></i>
            <input type="text" placeholder="请输入11位手机号码" v-model="username">
        </div>
        <p class="usernamemsg">{{usernamemsg}}</p>
        <div class="pwd">
            <i v-html="'&#xe802;'" class="iconfont"></i>
            <input type="password" placeholder="请输入5-20位数字或字母登录密码" v-model="userpwd">
        </div>
        <p class="userpwdmsg">{{userpwdmsg}}</p>
        <mt-button type="primary" size="large" :disabled = "loginbtnFlag" @click="loginIn()">登录</mt-button>
        <p class="path">
            <router-link :to="{name:'register'}">
                <span>注册账号</span>
            </router-link>
            <span>忘记密码？</span>
        </p>
    </div>
</template>


<script>
import store from "../vuex/store";
import { Toast } from 'mint-ui';
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    computed:{
        ...mapState(['username','userpwd','usernamemsg','userpwdmsg','loginbtnFlag','loginFlag','nickname']),
        username:{
            get(){
                return this.$store.state.username;
            },
            set(newval){
                // this.$store.commit("updatePhone",newval)
                this.updateUsername(newval)
            }
        },
        nickname:{
            get(){
                return this.$store.state.nickname;
            }
        },
        userpwd:{
            get(){
                return this.$store.state.userpwd;
            },
            set(newval){
                // this.$store.commit("updatePhone",newval)
                this.updateUserpwd(newval)
            }
        },
        
    },
    methods:{
        ...mapMutations({
            updateUsername:"getUsername",
            updateUserpwd:"getUserpwd"
        }),
        getUsername(e){
            console.log(e);
            this.$store.commit("getUsername",e);
        },
        getUserpwd(e){
            console.log(e);
            this.$store.commit("getUserpwd",e);
        },
        ...mapActions([
            'login'
        ]),
        loginIn(){
            this.login({url:'vue/login',username:this.username,userpwd:this.userpwd,callback:this.show})
        },
        show(){
            if(this.loginFlag.code == 2){
            Toast({
                message: this.loginFlag.msg,
                iconClass: 'icon icon-success'
            })
            localStorage.username = this.username;
            localStorage.nickname = this.nickname;
            this.$router.push('/app/mine')
            }else{
            Toast({
                message: this.loginFlag.msg
            })
            }
        }
        
    }
}
</script>




<style lang="scss" scoped>
    .section{
    padding-top: 80px;
    }
    .tel{
        width: 96%;
        margin: 10px auto;
        height: 100px;
        border:1px solid #ccc;
        box-sizing: border-box;
        padding: 20px 25px;
        background-color: #fff;
        i{
            display: inline-block;
            width: 60px;
            height: 60px;
            vertical-align: middle;
            line-height: 60px;
            font-size: 45px;
        }
        input{
            width: 500px;
            height: 60px;
            vertical-align: middle;
            font-size: 30px;
            line-height: 60px;
            border:0;
        }
    }
    .pwd{
        width: 96%;
        margin: 10px auto;
        height: 100px;
        border:1px solid #ccc;
        box-sizing: border-box;
        padding: 20px 25px;
        background-color: #fff;
        i{
            display: inline-block;
            width: 60px;
            height: 60px;
            vertical-align: middle;
            line-height: 60px;
            font-size: 45px;
        }
        input{
            width: 500px;
            height: 60px;
            vertical-align: middle;
            font-size: 30px;
            line-height: 60px;
            border:0;
        }
    }
    .mint-button--large{
        width: 96%;
        margin: 0 auto;
        margin-top: 20px;
    }
    .path{
        height: 80px;
        margin-top: 20px;
        line-height: 80px;
        font-size: 30px;
        span{
            float: left;
            width: 50%;
            text-align: center;
        }
    }
    .usernamemsg{
        width: 100%;
        height: 30px;
        box-sizing: border-box;
        line-height: 30px;
        padding:0 20px;
        color:red;
    }
    .userpwdmsg{
        width: 100%;
        height: 30px;
        box-sizing: border-box;
        line-height: 30px;
        padding:0 20px;
        color:red;
    }
</style>



