<template>
<div class="section">
    <Head :title="detail.name" showLeft="true"></Head>
    <div class="content">
        <h3>{{detail.name}}</h3>
        <p>主营：{{type}}</p>
        <p>人气：200</p>
        <p>点评数：10</p>
    </div>
    <div class="tel clearfix" v-for="(t,index) in detail.tels.split(',')" :key="index">
        <i class="iconfont left" v-html="'&#xe602;'"></i>
        <span>{{t}}</span>
        <i class="iconfont right" v-html="'&#xe64e;'"></i>
    </div>
    <div class="location clearfix">
        <i class="iconfont left" v-html="'&#xe62f;'"></i>
        <span>{{detail.address}}</span>
        <i class="iconfont right" v-html="'&#xe64e;'"></i>
    </div>
    <p class="comments">查看10条评论</p>
</div>
</template>


<script>
import store from "../vuex/store";
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    computed:{
        ...mapState(['type','detail']),
        type:{
            get(){
                return this.$store.state.type
            }
        },
        detail:{
            get(){
                return this.$store.state.detail
            }
        },
    },
    methods:{
        ...mapActions(['getDetail'])
    },
    created(){
        var url = window.location.href.split("?")[1]
        var key = decodeURI(url.split('&')[0].split("=")[1])
        var id = decodeURI(url.split('&')[1].split("=")[1])
        var type = decodeURI(url.split('&')[2].split("=")[1])
        console.log(key,id,type)
        this.getDetail({url:'vue/getDetail',key,id,type})
    }
}
</script>



<style lang="scss" scoped>
    .section{
        padding-top: 80px;
    }
    .content{
        width: 100%;
        padding: 30px;
        box-sizing: border-box;
        background-color: #fff;
        margin-top: 16px;;
        h3{
            width: 100%;
            font-size: 36px;
            text-align: center;
            color: rgb(240, 56, 32)
        }
        p{
            font-size: 28px;
        }
    }
    .tel{
        font-size: 32px;
        line-height: 40px;
        padding: 20px;
        background-color: #fff;
        border: 2px solid #f1f1f1;
        margin-top: 16px;
        .left{
            float: left;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: goldenrod;
            line-height: 40px;
            text-align: center;
        }
        .right{
            float: right;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            line-height: 40px;
            text-align: center;
        }
        span{
            float: left;
            margin-left: 20px;
        }
    }
    .location{
        font-size: 32px;
        line-height: 40px;
        background-color: #fff;
        border: 2px solid #f1f1f1;
        padding: 20px;
        .left{
            float: left;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: goldenrod;
            line-height: 40px;
            text-align: center;
        }
        .right{
            float: right;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            line-height: 40px;
            text-align: center;
        }
        span{
            float: left;
            margin-left: 20px;
        }
    }
    .comments{
        width: 100%;
        font-size: 28px;
        color:#333;
        border-top:2px solid #ddd;
        border-bottom:2px solid #ddd;
        line-height: 55px;
        margin-top: 16px;
        text-align: center;
        background-color: #fff;
    }
</style>



