<template>
    <div class="section">
        <Head :title="'修改密码'" showLeft="true"></Head>
        <div class="tel">
            <input type="text" placeholder="请输入11位手机号码" v-model="username">
        </div>
        <p class="usernamemsg">{{usernamemsg}}</p>
        <div class="oldPwd">
            <input type="password" placeholder="请输入4位数以上原登录密码" v-model="userpwd">
        </div>
        <p class="nicknamemsg">{{userpwdmsg}}</p>
        <div class="newPwd">
            <input type="password" placeholder="请输入4位数以上新登录密码"  v-model="newpwd">
        </div>
        <p class="userpwdmsg">{{newpwdmsg}}</p>
        <div class="newPwd2">
            <input type="password" placeholder="请重输一遍新登录密码"  v-model="newpwd2">
        </div>
        <p class="userpwd2msg">{{newpwd2msg}}</p>
        <mt-button type="primary" size="large" :disabled = "changePwdFlag" @click="changePwdIn()">确定修改</mt-button>
    </div>
</template>

<script>
import store from "../vuex/store";
import { Toast } from 'mint-ui';
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    computed:{
        ...mapState(['username','newpwd','newpwd2','usernamemsg','newpwdmsg','newpwd2msg','userpwd','userpwdmsg','changePwdFlag']),
        username:{
            get(){
                return this.$store.state.username;
            },
            set(newval){
                // this.$store.commit("updatePhone",newval)
                this.updateUsername(newval)
            }
        },
        userpwd:{
            get(){
                return this.$store.state.userpwd;
            },
            set(newval){
                this.updateUserpwd(newval)
            }
        },
        newpwd:{
            get(){
                return this.$store.state.newpwd;
            },
            set(newval){
                this.updateNewpwd(newval)
            }
        },
        newpwd2:{
            get(){
                return this.$store.state.newpwd2;
            },
            set(newval){
                this.updateNewpwd2(newval)
            }
        },
        changePwdFlag:{
            get(){
                return this.$store.state.changePwdFlag;
            }
        }
        
    },
    methods:{
        ...mapMutations({
            updateUsername:"getUsername",
            updateUserpwd:"getUserpwd",
            updateNewpwd2:"getNewpwd2",
            updateNewpwd:'getNewpwd'
        }),
        getUsername(e){
            console.log(e);
            this.$store.commit("getUsername",e);
        },
        getUserpwd(e){
            console.log(e);
            this.$store.commit("getUserpwd",e);
        },
        getNewpwd2(e){
            console.log(e);
            this.$store.commit("getNewpwd2",e);
        },
        getNewpwd(e){
            console.log(e);
            this.$store.commit("getNewpwd",e);
        },
        ...mapActions([
            'changePwd',
        ]),
        changePwdIn(){
            this.changePwd({url:'vue/changePwd',username:this.username,userpwd:this.userpwd,newpwd:this.newpwd,callback:this.show})
        },
        show(){
            if(this.changePwdFlag["code"] == 2){
            Toast({
                message: this.changePwdFlag.msg,
                iconClass: 'icon icon-success'
            })
            this.$router.push('/login')
            }else{
            Toast({
                message: this.changePwdFlag.msg
            })
            }
        }
        
    }
}
</script>




<style lang="scss" scoped>
    .section{
    padding-top: 80px;
    }
    .tel{
        width: 96%;
        margin: 10px auto;
        height: 100px;
        border:1px solid #ccc;
        box-sizing: border-box;
        padding: 20px 25px;
        background-color: #fff;
        i{
            display: inline-block;
            width: 60px;
            height: 60px;
            vertical-align: middle;
            line-height: 60px;
            font-size: 45px;
        }
        input{
            width: 500px;
            height: 60px;
            vertical-align: middle;
            font-size: 30px;
            line-height: 60px;
            border:0;
        }
    }
    .authCode{
        width: 96%;
        margin: 10px auto;
        height: 100px;      
        .left{
            float: left;
            width: 55%;
            border:1px solid #ccc;
            padding: 20px 25px;
            background-color: #fff;
            box-sizing: border-box;  
            i{
                display: inline-block;
                width: 60px;
                height: 60px;
                vertical-align: middle;
                line-height: 60px;
                font-size: 45px;
            }
            input{
                width: 240px;
                height: 60px;
                vertical-align: middle;
                font-size: 30px;
                line-height: 60px;
                border:0;
            }
        }
        .right{
            width: 42%;
            float: right;
            .send{
                display: block;
                width: 100%;
                height: 100px;
                border:0;
                background-color: green;
                color:white;
            }
        }
    }
    .newPwd{
        width: 96%;
        margin: 10px auto;
        height: 100px;
        border:1px solid #ccc;
        box-sizing: border-box;
        padding: 20px 25px;
        background-color: #fff;
        i{
            display: inline-block;
            width: 60px;
            height: 60px;
            vertical-align: middle;
            line-height: 60px;
            font-size: 45px;
        }
        input{
            width: 500px;
            height: 60px;
            vertical-align: middle;
            font-size: 30px;
            line-height: 60px;
            border:0;
        }
    }
    .newPwd2{
        width: 96%;
        margin: 10px auto;
        height: 100px;
        border:1px solid #ccc;
        box-sizing: border-box;
        padding: 20px 25px;
        background-color: #fff;
        i{
            display: inline-block;
            width: 60px;
            height: 60px;
            vertical-align: middle;
            line-height: 60px;
            font-size: 45px;
        }
        input{
            width: 500px;
            height: 60px;
            vertical-align: middle;
            font-size: 30px;
            line-height: 60px;
            border:0;
        }
    }
    .oldPwd{
        width: 96%;
        margin: 10px auto;
        height: 100px;
        border:1px solid #ccc;
        box-sizing: border-box;
        padding: 20px 25px;
        background-color: #fff;
        i{
            display: inline-block;
            width: 60px;
            height: 60px;
            vertical-align: middle;
            line-height: 60px;
            font-size: 45px;
        }
        input{
            width: 500px;
            height: 60px;
            vertical-align: middle;
            font-size: 30px;
            line-height: 60px;
            border:0;
        }
    }
    .mint-button--large{
        width: 96%;
        margin: 0 auto;
        margin-top: 20px;
    }
    p{
        height: 80px;
        margin-top: 20px;
        line-height: 80px;
        font-size: 30px;
        padding-left: 20px;
        span{
            width: 50%;
            text-align: center;
        }
    }
     .usernamemsg{
        width: 100%;
        height: 30px;
        box-sizing: border-box;
        line-height: 30px;
        padding:0 20px;
        color:red;
    }
     .userpwdmsg{
        width: 100%;
        height: 30px;
        box-sizing: border-box;
        line-height: 30px;
        padding:0 20px;
        color:red;
    }
     .userpwd2msg{
        width: 100%;
        height: 30px;
        box-sizing: border-box;
        line-height: 30px;
        padding:0 20px;
        color:red;
    }
     .nicknamemsg{
        width: 100%;
        height: 30px;
        box-sizing: border-box;
        line-height: 30px;
        padding:0 20px;
        color:red;
    }
</style>


