<template>
    <div class="section">
        <Head :title="splListTitleName" showRight="true" showLeft="true"></Head>
        <mt-loadmore :top-method="loadTop" ref="loadmore">
        <div class="btnBox clearfix">
            <mt-button type="danger" :disabled="username? false:true" @click="setPost">发帖</mt-button>
            <mt-button type="default" :disabled="username? false:true">我的帖子</mt-button>
            <mt-button type="default" :disabled="username? false:true">我的消息</mt-button>
        </div>
        <ul>
            <li v-for="(s,index) in splList" :key="index" class="clearfix" @click="gotoPost(s.id)">
                <h3>{{s.title}}</h3>
                <p class="user clearfix">
                    <span class="nick">{{s.nick}}</span>
                    <span class="review">
                        <i class="iconfont" v-html="'&#xe761;'"></i>
                        {{s.review}}
                    </span>
                    <span class="hot">
                        <i class="iconfont" v-html="'&#xe655;'"></i>
                        {{s.hot}}
                    </span>
                </p>
                <p class="brief">{{s.brief}}</p>
                <div class="img" v-for="(i,index) in s.imgs" :key="index">
                    <img v-lazy="i" alt="">
                </div>
            </li>
        </ul>
    </mt-loadmore>
    </div>
</template>

<script>
import store from "../vuex/store";
import { Toast } from "mint-ui"
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    computed:{
        ...mapState([
             'splList',
             'splListTitleName'
         ]),
         username:{
             get(){
                 return localStorage.username
             }
         }
    },
    methods:{
        ...mapActions(['getSplList']),
        gotoPost(id){
            this.$router.push('/post?id='+id)
        },
        setPost(){
            this.$router.push({name:'setPost'})
        },
        loadTop(){
            console.log('下拉刷新')
            setTimeout(()=>{
                Toast('下拉刷新数据成功!');
                this.getSplList({url:'vue/splList',msg:this.splListTitleName});
                    this.$refs.loadmore.onTopLoaded();
            },1500)
        },
    },
    created(){
        var url = window.location.href.split("?")[1]
        var msg = decodeURI(url.split('=')[1])
        this.getSplList({url:'vue/splList',msg});
    }
}
</script>



<style lang="scss" scoped>
    .section{
        padding-top: 80px;
    }
    .btnBox{
        width: 100%;
        padding: 20px 30px;
        box-sizing: border-box;
        background-color: #fff;
        border-bottom: 2px solid #d7d7d7;
        .mint-button {
            height: 66px;
        }
        .mint-button--danger {
            float: left;
            background-color: #ff6634;
        }
        .mint-button--default {
            float: right;
            margin-right: 30px;
        }
    }
    
    ul{
        width: 100%;
        li{
            width: 100%;
            padding: 32px;
            box-sizing: border-box;
            background-color: #fff;
            h3{
                font-size: 40px;
                line-height: 60px;
                color:#0066ca;
                border-bottom: 2px solid #e1e1e1;
            }
            .user{
                width: 100%;
                line-height: 48px;
                .nick{
                    font-size: 26px;
                    color:#a0a0a0;
                    float: left;
                }
                .hot{
                    float:right;
                    font-size: 26px;
                    color:#a0a0a0;
                    margin-right: 30px;
                }
                .review{
                    float:right;
                    font-size: 26px;
                    color:#a0a0a0;
                }
            }
            .brief{
                color:#4d4d4d;
                width: 100%;
                line-height: 60px;
                font-size: 36px;
            }
            .img{
                width: 31%;
                padding: 6px;
                float: left;
                img{
                    width: 100%;
                }
            }
        }
    }

</style>


