<template>
    <div class="section">
        <img src="../../assets/images/guide.png" alt="">
    </div>
</template>

<script>
export default {
    methods:{
        gotoHome(){
            setTimeout(()=>{
                this.$router.push({name:'home'})
            },3000)
        }
    },
    mounted(){
       this.gotoHome()
    }
}
</script>




<style lang="scss" scoped>
    img{
        width: 100%;
        height: 100%;
    }
</style>




