

// 更改 Vuex 的 store 中的状态的唯一方法是提交 mutation
//  mutation 非常类似于事件
// 每个 mutation 都有一个字符串的 事件类型 (type) 和 一个 回调函数 (handler)

// 接受 state 作为第一个参数

   // 对象 是  可变对象  
// 修改可变的对象 vue 视图系统无法检测  
// state.obj = { ...state.obj, newProp: 123 }
export default {
    add(){

    },
    increment(state){
        console.log(state);
        console.log("mutation-type");
        state.number++;
    },
    decrement(state){
        state.number--;
    },
    countadd(state,preload){
        state.number+=preload;
    },
    changeCity(state,city){
        state.city = city;
    },
    changeMsg(state,{msg}){
     
        state.msg = msg;  
        // staet.msg = {...state.msg,msg:msg}
    },
    changeUsername(state,{username}){
        state.obj = {...state.obj,username:username};// 对象  
    },
    changeData(state,data){
        state.data = data;
    },
    getmv(state,mv){
        state.mv = mv;
    },
    getUsername(state,username){
        state.username = username;
        var reg = /^[1][3,4,5,7,8][0-9]{9}$/;
        if(reg.test(username)){
            if(localStorage.username){
                console.log(localStorage.username)
                if(localStorage.username != username){
                    state.usernamemsg = "只可修改自身账号的密码"
                }else{
                    state.usernamemsg = ""
                }
                
            }else{
                state.usernamemsg = ""
            }
            
        }else{
            state.usernamemsg = "手机号码格式不正确"
        }
        if(state.userpwd != ""&&state.username != ""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""){
                state.loginbtnFlag = false;
            }else{
                state.loginbtnFlag = true;
            }
        }else{
            state.loginbtnFlag = true;
        }
        if(state.username != ""&&state.userpwd != ""&&state.newpwd != ""&&state.newpwd2 != ""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""&state.newpwd2msg == "" && state.newpwdmsg == ""){
                state.changePwdFlag = false;
            }else{
                state.changePwdFlag = true;
            }
        }else{
            state.changePwdFlag = true;
        }
        if(state.username != ""&&state.userpwd != ""&&state.nickname != ""&&state.userpwd2 != ""&&state.authCode !=""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""&state.userpwd2msg == "" && state.nicknamemsg == ""&&state.check&&state.authCodeMsg==""){
                state.registerbtnFlag = false;
            }else{
                state.registerbtnFlag = true;
            }
        }else{
            state.registerbtnFlag = true;
        }
        console.log(state);
    },
    getNickname(state,nickname){
        state.nickname = nickname;
        if(nickname != ""&&nickname != " "){
            state.nicknamemsg = ""
        }else{
            state.nicknamemsg = "昵称不可为空"
        }
        if(state.username != ""&&state.userpwd != ""&&state.nickname != ""&&state.userpwd2 != ""&&state.authCode !=""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""&state.userpwd2msg == "" && state.nicknamemsg == ""&&state.check&&state.authCodeMsg==""){
                state.registerbtnFlag = false;
            }else{
                state.registerbtnFlag = true;
            }
        }else{
            state.registerbtnFlag = true;
        }
        console.log(state);
    },
    getUserpwd(state,userpwd){
        state.userpwd = userpwd;
        var reg = /^[a-zA-Z0-9]{5,20}$/;
        if(reg.test(userpwd)){
            console.log(reg.test(userpwd))
            state.userpwdmsg = ""
        }else{
            state.userpwdmsg = "密码格式不正确"
        }
        if(state.userpwd != ""&&state.username != ""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""){
                state.loginbtnFlag = false;
            }else{
                state.loginbtnFlag = true;
            }
        }else{
            state.loginbtnFlag = true;
        }
        if(state.username != ""&&state.userpwd != ""&&state.newpwd != ""&&state.newpwd2 != ""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""&state.newpwd2msg == "" && state.newpwdmsg == ""){
                state.changePwdFlag = false;
            }else{
                state.changePwdFlag = true;
            }
        }else{
            state.changePwdFlag = true;
        }
        if(state.username != ""&&state.userpwd != ""&&state.nickname != ""&&state.userpwd2 != ""&&state.authCode !=""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""&state.userpwd2msg == "" && state.nicknamemsg == ""&&state.check&&state.authCodeMsg==""){
                state.registerbtnFlag = false;
            }else{
                state.registerbtnFlag = true;
            }
        }else{
            state.registerbtnFlag = true;
        }
        console.log(state);
    },
    getNewpwd(state,newpwd){
        state.newpwd = newpwd;
        var reg = /^[a-zA-Z0-9]{5,20}$/;
        if(reg.test(newpwd)){
            console.log(reg.test(newpwd))
            state.newpwdmsg = ""
        }else{
            state.newpwdmsg = "密码格式不正确"
        }
        if(state.username != ""&&state.userpwd != ""&&state.newpwd != ""&&state.newpwd2 != ""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""&state.newpwd2msg == "" && state.newpwdmsg == ""){
                state.changePwdFlag = false;
            }else{
                state.changePwdFlag = true;
            }
        }else{
            state.changePwdFlag = true;
        }
        console.log(state);
    },
    setMsgCode(state,msgCode){
        state.msgCode = msgCode
    },
    getAuthCode(state,authCode){
        state.authCode = authCode;
        console.log(authCode)
        if(authCode.length == 4){
            if(authCode == state.msgCode){
                state.authCodeMsg = ""
            }else{
                state.authCodeMsg = "手机验证码不正确"
            }    
        }else{
            state.authCodeMsg = "手机验证码格式不正确"
        }
        if(state.username != ""&&state.userpwd != ""&&state.nickname != ""&&state.userpwd2 != ""&&state.authCode !=""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""&state.userpwd2msg == "" && state.nicknamemsg == ""&&state.check&&state.authCodeMsg==""){
                state.registerbtnFlag = false;
            }else{
                state.registerbtnFlag = true;
            }
        }else{
            state.registerbtnFlag = true;
        }
    },
    getNewpwd2(state,newpwd2){
        state.newpwd2 = newpwd2;
        var reg = /^[a-zA-Z0-9]{5,20}$/;
        if(reg.test(newpwd2)){
            if(newpwd2 == state.newpwd){
                state.newpwd2msg = ""
            }else{
                state.newpwd2msg = "两次输入密码不一致"
            }            
        }else{
            state.newpwd2msg = "密码格式不正确"
        }
        console.log(state);
        if(state.username != ""&&state.userpwd != ""&&state.newpwd != ""&&state.newpwd2 != ""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""&state.newpwd2msg == "" && state.newpwdmsg == ""){
                state.changePwdFlag = false;
            }else{
                state.changePwdFlag = true;
            }
        }else{
            state.changePwdFlag = true;
        }
    },
    getUserpwd2(state,userpwd2){
        state.userpwd2 = userpwd2;
        var reg = /^[a-zA-Z0-9]{5,20}$/;
        if(reg.test(userpwd2)){
            if(userpwd2 == state.userpwd){
                state.userpwd2msg = ""
            }else{
                state.userpwd2msg = "两次输入密码不一致"
            }            
        }else{
            state.userpwd2msg = "密码格式不正确"
        }
        if(state.username != ""&&state.userpwd != ""&&state.nickname != ""&&state.userpwd2 != ""&&state.authCode !=""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""&state.userpwd2msg == "" && state.nicknamemsg == ""&&state.check&&state.authCodeMsg==""){
                state.registerbtnFlag = false;
            }else{
                state.registerbtnFlag = true;
            }
        }else{
            state.registerbtnFlag = true;
        }
        console.log(state);
    },
    getCheck(state,check){
        state.check = check;
        if(state.username != ""&&state.userpwd != ""&&state.nickname != ""&&state.userpwd2 != ""&&state.authCode !=""){
            if(state.usernamemsg == "" && state.userpwdmsg == ""&state.userpwd2msg == "" && state.nicknamemsg == ""&&state.check&&state.authCodeMsg==""){
                state.registerbtnFlag = false;
            }else{
                state.registerbtnFlag = true;
            }
        }else{
            state.registerbtnFlag = true;
        }
        
        console.log(state);
    },
    login(state,loginFlag){
        state.loginFlag = loginFlag,
        state.nickname = loginFlag.nickname
    },
    changeNickname(state,val){
        state.nickname = val
    },
    chooseReferrer(state,val){
        state.referrer = val
    },
    changeSex(state,sex){
        state.sex = sex
    },
    register(state,registerFlag){
        state.registerFlag = registerFlag
    },
    getUserinfo(state,userinfo){
        state.sex = userinfo[0].sex;
        state.referrer = userinfo[0].referrer;
        state.nickname = userinfo[0].nickname;
    },
    getHomeContent(state,homeContent){
        state.homeContent = homeContent;
        state.news = homeContent[0].news;
        state.bannerSrc = homeContent[0].slide.img;
        state.shop = homeContent[0].icos.shop;
        state.vouchers = homeContent[0].icos.vouchers;
        state.bbs = homeContent[0].icos.bbs;
        state.citychosen = homeContent[0].icos.citychosen;
        state.splcontent4 = homeContent[0].bbs[2].title;
        state.splcontent3 = homeContent[0].bbs[1].title;
        state.splcontent2 = homeContent[0].bbs[0].title;
        state.splcontent1 = homeContent[0].bbs_img.title;
        state.splimg = homeContent[0].bbs_img.img;
        state.citysimg1 = homeContent[0].citys[0].img;
        state.citysimg2 = homeContent[0].citys[1].img;
        state.citysimg3 = homeContent[0].citys[2].img;
        state.citysimg4 = homeContent[0].citys[3].img;
        state.apply = homeContent[0].apply;
    },
    getService(state,service){
        state.service = service;
        state.hot = service[0].hot;
        state.homemaking = service[0].list.list_4;
        state.cate = service[0].list.list_7;
        state.recreation = service[0].list.list_6;
        state.hotel = service[0].list.list_5;
        state.hairdressing = service[0].list.list_3;
        state.car = service[0].list.list_2;
        state.shopping = service[0].list.list_87;
    },
    getCityServe(state,serve){
        state.cityServe = serve[0]
        state.cityServeTitleName = serve[1]
    },
    getSplList(state,serve){
        state.splList = serve[0]
        state.splListTitleName = serve[1]
    },
    getList(state,serve){
        state.list = serve[0];
        state.titleName = serve[1]
    },
    changePwd(state,registerFlag,newpwd){
        state.changePwdFlag = registerFlag;
        if(registerFlag.code == 2){
            state.userpwd = newpwd
        }
    },
    getDetail(state,val){
        state.detail = val[0];
        state.type = val[1]
    },
    getPost(state,post){
        state.post = post[0]
    },
    changeReviewMsg(state,reviewMsg){
        state.reviewMsg = reviewMsg
    },
    getReview(state,review){
        state.review = review
    },
    changeTitle(state,title){
        state.postTitle = title
    },
    changeContent(state,content){
        state.postContent = content
    },
}


