<template>
    <div class="section">
        <a class="picture clear">
            <div class="charhead iconfont" v-html="'&#xe636;'"></div>
            <mt-button class="login" @click="gotologin" v-if="username? false:true">登录</mt-button>
            <mt-button class="user" @click="gotoUserinfo" v-if="username? true:false">{{nickname}}</mt-button>
        </a>
        <div class="msg">
            <span>主人，可以领金豆了，</span>
            <br>
            <span>存起来以后当私房钱用吧！</span>
            <button class="btn">马上领取</button>
        </div>
        <ul class="nav">
            <li>
                <i v-html="'&#xe610;'" class="iconfont indent"></i>
                <span>我的特惠购订单</span>
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
            </li>
            <li>
                <i v-html="'&#xe639;'" class="iconfont gold"></i>
                <span>我的金豆</span>
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
            </li>
            <li>
                <i v-html="'&#xe60f;'" class="iconfont weal"></i>
                <span>我领取的金豆福利</span>
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
            </li>
            <li>
                <i v-html="'&#xe65d;'" class="iconfont collect"></i>
                <span>商户收藏</span>
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
            </li>
            <li>
                <i v-html="'&#xe618;'" class="iconfont message"></i>
                <span>我的消息</span>
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
            </li>
        </ul>
    </div>
</template>



<script>
import store from "../vuex/store";
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    methods:{
        gotologin(){
            this.$router.push({name:"login"})
        },
        gotoUserinfo(){
            this.$router.push({name:"userinfo"})
        }
    },
    computed:{
        ...mapState(['nickname']),
        username:{
             get(){
                 return localStorage.username
             }
         },
         nickname:{
             get(){
                 return localStorage.nickname
             }
         }
    }
}
</script>

<style lang="scss" scoped>
.picture{
    width: 100%;
    height: 280px;
    display: block;
    background: url(../../assets/images/chathead.jpg);
    background-size: 100% 100%;
    text-align: center;
    .charhead{
        width: 120px;
        height: 120px;
        border-radius: 50%;
        line-height: 120px;
        color:white;
        margin:0 auto;
        margin-top: 50px;
        margin-bottom: 15px;
        font-size: 80px;
        text-align: center;
        overflow: hidden;
        background-color: #c0e6d7;
    }
    .login{
        width: 130px;
        height: 50px;
        font-size: 26px;
        line-height: 50px;
        border-radius: 2px;
        border:2px solid white;
        color:white;
        background: transparent;
    }
    .user{
        width: 130px;
        height: 50px;
        font-size: 26px;
        line-height: 50px;
        color:white;
        background: transparent;
    }
}
.msg{
    width: 100%;
    height: 150px;
    font-size: 26px;
    box-sizing: border-box;
    padding:30px 25px;
    background-color: #f6f6f6;
    .btn{
        display: block;
        width: 200px;
        height: 60px;
        float: right;margin-top: -20px;
        border:0;
        background-color: #ff6600;
        color:white;
    }
}
.nav{
    width: 100%;
    padding-top: 20px;
    li{
        width: 100%;
        height: 100px;
        box-sizing: border-box;
        margin-top: 15px;
        border-top:2px solid #ddd;
        border-bottom:2px solid #ddd;
        padding:20px;
        font-size: 30px;
        background-color: #fff;
        i{
            display: inline-block;
            vertical-align: middle;
            text-align: center;
            color:#fff;
            line-height: 60px;
            border-radius: 50%;
            width: 60px;
            height: 60px;
        }
        span{
            padding-left: 10px;
            display: inline-block;
            vertical-align: middle;
            line-height: 60px;
        }
        .indent{
            background-color: #7d83ff;
        }
        .gold{
            background-color: #f86900;
        }
        .weal{
            background-color: #68ddd5;
        }
        .collect{
            background-color: #d29350;
        }
        .message{
            background-color: #56b2ff;
        }
        .path{
            float:right;
            text-align: center;
            width: 60px;
            height: 60px;
            line-height: 60px;
            color: #c5c5c5;
        }
    }
}
</style>





