<template>
    <div class="section">
        <Head :title="'账号管理'" showLeft="true"></Head>
        <ul class="nav">
            <li>
                <span class="left">头像</span>
                <span class="right">
                <i class="charhead iconfont" v-html="'&#xe636;'"></i>    
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
                </span>
            </li>
            <li @click="changeNickname">
                <span class="left">昵称</span>
                <span class="right">
                    {{nickname}}
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
                </span>
            </li>
            <li @click="chooseSex">
                <span class="left">性别</span>
                <span class="right">
                {{sex}}    
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
                </span>
            </li>
            <li>
                <span class="left">手机号验证</span>
                <span class="right">
                    
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
                </span>
            </li>
            <li @click="Referrer">
                <span class="left">我的推荐人</span>
                <span class="right">
                {{referrer?referrer:'未设置'}}    
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
                </span>
            </li>
            <li @click="changePwd">
                <span class="left">修改密码</span>
                <span class="right">
                    
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
                </span>
            </li>
        </ul>
        <mt-button type="danger" size="large" @click="logout">退出登录</mt-button>
        <mt-actionsheet
            :actions="data"
            v-model="sheetVisible">
        </mt-actionsheet>
    </div>
</template>


<script>
import store from "../vuex/store";
import { MessageBox,Toast,Actionsheet  } from 'mint-ui';
import axios from "axios";
import { mapState, mapMutations, mapActions } from "vuex";
export default {
    // name:"section",
    data(){
        return {
            data: [{
                    name: '男',
                    method : this.men
                }, {
                    name: '女', 
                    method : this.women
                }],
                sheetVisible: false
            }

        },
    computed:{
        ...mapState(['sex','referrer','nickname']),
        nickname:{
            get(){
                return this.$store.state.nickname || localStorage.nickname;
            }
        },
        sex:{
            get(){
                return this.$store.state.sex;
            }
        },
        referrer:{
            get(){
                return this.$store.state.referrer;
            }
        },
        Referrer:{
            get(){
                return this.referrer?null:this.chooseReferrer
            }
        },
        username:{
            get(){
                return localStorage.username;
            }
        }
    },
    methods:{
        ...mapActions(['changeSex','getUserinfo']),
        changeNickname(){
            MessageBox.prompt('设置昵称').then(({value})=>{
                
                    axios.get('http://118.31.74.14:3080/vue/changeNickname',{
                        params:{
                            nickname:value,
                            username:this.username
                        }
                    }).then(res=>{
                        // console.log(res.data);
                        // commit("login",res.data);
                        // console.log(callback);
                        // callback();
                        if(res.data.code == 2){
                            this.$store.commit("changeNickname",value);
                            localStorage.nickname = this.nickname
                            Toast({
                                message: res.data.msg,
                                iconClass: 'icon icon-success'
                            })
                        }else{
                            Toast({
                                message: res.data.msg,
                                iconClass: 'icon icon-success'
                            })
                        }
                        
                    })
            })
        },
        chooseReferrer(){
            MessageBox.prompt('设置推荐人(电话号码)').then(({value})=>{
                
                    axios.get('http://118.31.74.14:3080/vue/chooseReferrer',{
                        params:{
                            referrer:value,
                            username:this.username
                        }
                    }).then(res=>{
                        // console.log(res.data);
                        // commit("login",res.data);
                        // console.log(callback);
                        // callback();
                        this.$store.commit("chooseReferrer",value);
                        Toast({
                            message: res.data,
                            iconClass: 'icon icon-success'
                        })
                    })
            })
        },
        chooseSex(){
            this.sheetVisible = !this.sheetVisible
        },
        men(){
            this.changeSex({url:'vue/changeSex',sex:'男',username:this.username})
        },
        women(){
            this.changeSex({url:'vue/changeSex',sex:'女',username:this.username})
        },
        changePwd(){
            this.$router.push({name:'changePwd'})
        },
        logout(){
            MessageBox.confirm('确定要退出账号么？').then(()=>{
                localStorage.removeItem('username');
                localStorage.removeItem('nickname');
                this.$router.push({name:'login'})
            }) 
        }
    },
    mounted(){
        this.getUserinfo({url:'vue/userinfo',username:this.username})
    }
};
</script>


<style lang="scss" scoped>
.section {
  padding-top: 80px;
}
.nav {
  width: 100%;
  border-top: 2px solid #b2b2b2;
  li {
    width: 100%;
    height: 100px;
    line-height: 100px;
    box-sizing: border-box;
    border-bottom: 2px solid #ddd;
    font-size: 30px;
    background-color: #fff;
    i {
      display: inline-block;
      vertical-align: middle;
      text-align: center;
      font-size: 45px;
      color: #929292;
      line-height: 60px;
      border-radius: 50%;
      width: 60px;
      height: 60px;
    }
    .left {
      padding-left: 10px;
      display: inline-block;
      vertical-align: middle;
      line-height: 60px;
    }
    .right {
      float: right;
      .charhead {
        display: inline-block;
        vertical-align: middle;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        line-height: 80px;
        color: white;
        font-size: 80px;
        text-align: center;
        overflow: hidden;
        background-color: #c0e6d7;
      }
      .path {
        display: inline-block;
        vertical-align: middle;
        text-align: center;
        width: 60px;
        height: 60px;
        line-height: 60px;
        color: #c5c5c5;
      }
    }
  }
}
</style>




