<template>
<div class="section">
    <header>
        <div class="scan">
            <i class="iconfont" v-html="'&#xe62b;'"></i>
            扫一扫
        </div>
        <div class="logo">
            <img src="../../assets/images/logo.png" alt="">
        </div>
        <div class="msg">
            <i class="iconfont" v-html="'&#xe618;'"></i>
            消息
        </div>
        
    </header>
    <mt-loadmore :top-method="loadTop" ref="loadmore">
    <img v-lazy="bannerSrc" alt="" class="banner">
    <swiper class="swiper-wrapper"  :options="swiperOption" ref="mySwiper">
        <swiper-slide class="swiper-slide" v-for="(n,index) in news " :key="index">
            <span class="title">{{n.tag=='local'?'本地':'热点'}}</span>
            <span>{{n.title}}</span>
        </swiper-slide>
    </swiper>
    <div class="service clearfix">
        <div class="serve" @click="gotoCityServe('城市精选')">
            <img v-lazy="citychosen" alt="">
            <p>城市精选</p>
        </div>
        <div class="serve" @click="gotoSplList('四牌楼')">
            <img v-lazy="bbs" alt="">
            <p>四牌楼</p>
        </div>
        <div class="serve" @click="gotoGoldWeal('金豆福利社')">
            <img v-lazy="vouchers" alt="">
            <p>金豆福利社</p>
        </div>
        <div class="serve" @click="gotoCityServe('找商家')">
            <img v-lazy="shop" alt="">
            <p>找商家</p>
        </div>
    </div>
    <div class="nav">
        <div class="left">
            <img src="../../assets/images/nav1.jpg" alt="">
        </div>
        <div class="right">
            <div class="nav2">
                <img src="../../assets/images/nav2.png" alt="">
            </div>
            <div class="nav2" >
                <img src="../../assets/images/nav3.png" alt="">
            </div>
            <div class="nav2">
                <img src="../../assets/images/nav4.png" alt="">
            </div>
            <div class="nav2">
                <img src="../../assets/images/nav5.png" alt="">
            </div>
        </div>
    </div>
    <div class="spl" @click="gotoSplList('四牌楼')">
        <div class="stitle clearfix">
            <div class="left">四牌楼</div>
            <div class="right">
                <span>进入</span>
                <i v-html="'&#xe64e;'" class="iconfont"></i>
            </div>
        </div>
        <div class="sContent clearfix">
            <div class="left">
                <img v-lazy="splimg" alt="">
                <p>{{splcontent1}}</p>
            </div>
            <div class="right">
                <p class='msg'>{{splcontent2}}</p>
                <p class='msg'>{{splcontent3}}</p>
                <p class='msg'>{{splcontent4}}</p>
            </div>
        </div>
    </div>
    <div class="citys">
        <div class="ctitle clearfix" @click="gotoCityServe('城市精选')">
            <div class="left">城市精选</div>
            <div class="right">
                <span>进入</span>
                <i v-html="'&#xe64e;'" class="iconfont"></i>
            </div>
        </div>
        <div class="citysContent clearfix">
            <div class="cityContent">
                <img v-lazy="citysimg1" alt="">
            </div>
            <div class="cityContent">
                <img v-lazy="citysimg2" alt="">
            </div>
            <div class="cityContent">
                <img v-lazy="citysimg3" alt="">
            </div>
            <div class="cityContent">
                <img v-lazy="citysimg4" alt="">
            </div>
        </div>
    </div>
    <div class="apply">
        <div class="atitle clearfix">
            <div class="left">本地服务</div>
        </div>
        <div :class="checked?'applyContent add clearfix':'applyContent clearfix'">
            <div class="content" v-for="(app,index) in apply" :key="index">
                <div class="left">
                    <img v-lazy="app.img" alt="">
                </div>
                <div class="right">
                    <h3>{{app.name}}</h3>
                    <p>{{app.detail}}</p>
                </div>
            </div>
        </div>
        
        <label for="checkicon" class="iconfont" v-html="checked?'&#xe814;':'&#xe604;'" @click="changeChecked">
            <input type="checkbox" v-model="checked" id="checkicon" hidden>
        </label>
    </div>
    </mt-loadmore>
</div>  
</template>



<script>

import store from "../vuex/store";
import { Toast } from "mint-ui"
import {mapState,mapMutations,mapActions} from "vuex";
import { swiper, swiperSlide } from 'vue-awesome-swiper'


export default {
    data(){
        return {
            swiperOption: {
                autoplay: true,
                direction: "vertical",
                loop:true,
                speed: 400,
            },
            checked:false
        }
    },
    computed:{
        ...mapState([
            'homeContent',
            'news',
            'bannerSrc',
            'citychosen',
            'bbs',
            'vouchers',
            'shop',
            'splimg',
            'splcontent1',
            'splcontent2',
            'splcontent3',
            'splcontent4',
            'citysimg1',
            'citysimg2',
            'citysimg3',
            'citysimg4',
            'apply',
            'checked'
            ]),
        homeContent:{
            get(){
                return this.$store.state.homeContent;
            }
        },
        bannerSrc:{
            get(){
                return this.$store.state.bannerSrc;
            }
        },
        
        citychosen:{
            get(){
                return this.$store.state.citychosen;
            }
        },
        bbs:{
            get(){
                return this.$store.state.bbs;
            }
        },
        vouchers:{
            get(){
                return this.$store.state.vouchers;
            }
        },
        shop:{
            get(){
                return this.$store.state.shop;
            }
        },
        splcontent1:{
            get(){
                return this.$store.state.splcontent1;
            }
        },
        splimg:{
            get(){
                return this.$store.state.splimg;
            }
        },
        splcontent2:{
            get(){
                return this.$store.state.splcontent2;
            }
        },
        splcontent3:{
            get(){
                return this.$store.state.splcontent3;
            }
        },
        splcontent4:{
            get(){
                return this.$store.state.splcontent4;
            }
        },
        citysimg1:{
            get(){
                return this.$store.state.citysimg1;
            }
        },
        citysimg2:{
            get(){
                return this.$store.state.citysimg2;
            }
        },
        citysimg3:{
            get(){
                return this.$store.state.citysimg3;
            }
        },
        citysimg4:{
            get(){
                return this.$store.state.citysimg4;
            }
        },
        apply:{
            get(){
                return this.$store.state.apply;
            }
        },
        swiper () {
        return this.$refs.mySwiper.swiper
      }
    },
    methods:{
        ...mapActions(['getHomeContent']),
        changeChecked(){
            if(this.checked){
                this.checked = false
            }else{
                this.checked = true
            }
        },
        loadTop(){
            console.log('下拉刷新')
            setTimeout(()=>{
                this.getHomeContent({url:'vue/home'});
                Toast('下拉刷新数据成功!');
                    this.$refs.loadmore.onTopLoaded();
            },1500)
        },
        gotoCityServe(msg){
            this.$router.push("/cityServe?msg="+msg)
        },
        gotoSplList(msg){
            this.$router.push('/splList?msg='+msg);
        },
        gotoGoldWeal(msg){
            this.$router.push('/goldWeal?msg='+msg);
        }
    },
    
    mounted(){  
        this.getHomeContent({url:'vue/home'});
        console.log()
       
    }
}
</script>



<style lang="scss" scoped>
    .section{
        margin-top: 80px;
        margin-bottom: 90px;
    }
    header{
        width: 100%;
        height: 80px;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 1000;
        background: url(../../assets/images/bg.png) repeat 0 0;
        .scan{
            width: 70px;
            float: left;
            text-align: center;
            font-size: 20px;
            color:#a1a1a1;
            padding: 0 30px;
            padding-top: 10px;
            i{
                display: block;
                width: 70px;
                text-align: center;
                font-size: 40px;
            }
        }
        .msg{
        width: 70px;
            float: right;
            text-align: center;
            font-size: 20px;
            color:#a1a1a1;
            padding: 0 30px;
            padding-top: 10px;
            i{
                display: block;
                width: 70px;
                text-align: center;
                font-size: 40px;
            }
        }
        .logo{
            position: absolute;
            left: 50%;
            top: 10px;
            img{
                width: 200px;
                margin-left: -100px;;
            }
        }
    }
    .banner{
        width: 100%;
    }
    .swiper-wrapper{
        width: 100%;
        height: 80px;
        background-color: #fff;
        .swiper-slide{
        width: 100%;
        height: 80px;
        padding:20px 20px;
        box-sizing: border-box;
        color:#4d4d4d;
        .title{
        display: inline-block;
        width: 60px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color:#d3232d;
        border:2px solid #cda1a2;
        border-radius: 6px;
        }
    }
    }
    .service{
        width: 100%;
        background-color: #fff;
        border-top: 2px solid #e6e6e6;
        .serve{
            float: left;
            text-align: center;
            width: 25%;
            padding: 30px 0;
            img{
                width: 80px;
                display: inline;
            }
        }
    }
    .nav{
        width: 100%;
        height: 260px;
        margin-top: 20px;
        .left{
            width: 36%;
            float: left;
            height: 260px;
            border:1px solid #eee;
            box-sizing: border-box;
            img{
                width: 100%;
                height: 260px;
                vertical-align: top;
            }
        }
        .right{
            width: 64%;
            float: left;
            font-size: 0;
            .nav2{
                width: 50%;
                float: left;
                height: 130px;
                border:1px solid #eee;
                box-sizing: border-box;
                img{
                    width: 100%;
                    height: 130px;
                    vertical-align: middle;
                }
            }
        }
    }
    .spl{
        width: 100%;
        margin-top: 25px;
        background-color: green;
        .stitle{
            margin-left: 30px;
            background: url(../../assets/images/spl.png) 50% 0 no-repeat;
            background-color: #fafafa;
            background-size: 60% 100%;
            .left{
                height: 90px;
                float: left;
                padding-left: 30px;
                line-height: 90px;
                font-size: 32px;
            }
            .right{
                float:right;
                height: 90px;
                margin-right: 26px;;
                span{
                    font-size: 24px;
                    line-height: 90px;
                }
                i{
                    display: inline;
                    line-height: 90px;
                    color:#eee;
                    vertical-align: middle;
                    background-color: green;
                    border-radius: 4px;
                }
            }
        }
        .sContent{
            width: 100%;
            background-color: #fff;
            
            padding: 30px;
            box-sizing: border-box;
            .left{
                width: 35%;
                height: 300px;
                float: left;
                img{
                    width: 100%;
                    height: 180px;
                }
                p{
                    font-size: 36px;
                    height: 120px;
                    line-height: 60px;
                    color:#ff6600;
                    overflow : hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                }
            }
            .right{
                width: 65%;
                float: left;
                padding-left: 30px;
                box-sizing: border-box;
                .msg{
                    width: 100%;
                    height: 100px;
                    line-height: 100px;
                    font-size: 36px;
                    border-bottom: 2px solid #f2f2f2;
                    text-indent: 20px;
                }
            }
            
        }
    }
    .citys{
        width: 100%;
        margin-top: 25px;
        background-color: #ff6600;
        .ctitle{
            margin-left: 30px;
            background-color: #fafafa;
            background-size: 60% 100%;
            .left{
                height: 90px;
                float: left;
                padding-left: 30px;
                line-height: 90px;
                font-size: 32px;
            }
            .right{
                float:right;
                height: 90px;
                margin-right: 26px;;
                span{
                    font-size: 24px;
                    line-height: 90px;
                }
                i{
                    display: inline;
                    line-height: 90px;
                    color:#eee;
                    vertical-align: middle;
                    background-color: #ff6600;
                    border-radius: 4px;
                }
            }
        }
        .citysContent{
            width: 100%;
            padding:20px;
            background-color: #fff;
            box-sizing: border-box;
            .cityContent{
                width: 50%;
                float: left;
                padding:10px;
                box-sizing: border-box;
                img{
                    width: 100%;
                }
            }
        }
    }
    .apply{
        width: 100%;
        margin-top: 25px;
        background-color: #31c8ff;
        .atitle{
            margin-left: 30px;
            background-color: #fafafa;
            background-size: 60% 100%;
            .left{
                height: 90px;
                float: left;
                padding-left: 30px;
                line-height: 90px;
                font-size: 32px;
            }
        }
        .applyContent{
            width: 100%;
            height: 560px;
            overflow: hidden;
            background-color: #fff;
            box-sizing: border-box;
            border-top: 2px solid #e6e6e6;
            .content{
                width: 50%;
                float: left;
                padding: 30px 0;
                padding-left: 20px;
                box-sizing: border-box;
                border-bottom: 2px solid #e6e6e6;
                .left{
                    img{
                        width: 80px;
                        float: left;
                    }
                }
                .right{
                    padding-left: 20px;
                    float: left;
                    h3{
                        font-size: 32px;
                    }
                    p{
                        color:#a9a9a9;
                    }
                }
            }
        }
        .add{
            height: 1120px;
        }
        label{
            display: block;
            width: 100%;
            font-size: 28px;
            background-color: #fff;
            text-align: center;
            line-height: 60px;
        }
    }
    
</style>




