<template>
    <mt-header :title="title">
        <mt-button icon="back" slot="left" @click="goback" v-if="showLeft" ></mt-button>
        <div class="search iconfont"  slot="right" v-html="'&#xe718;'" v-if="showRight"></div>
    </mt-header>
</template>
<script>
import router from "../router";
export default {
    props:["title","showLeft",'showRight'],
    methods:{
        goback(){
            console.log(this.$router);
            // this.$router.go(-1);
            router.go(-1);
        }
    }
}
</script>

<style lang="scss" scoped>
    .mint-header{
        width: 100%;
        box-sizing: border-box;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 1000;
        background-color: #f8f8f8;
        color: #4d4d4d;
        font-size: 34px;
        border-bottom: 2px solid #b2b2b2;
    }
    .search{
        font-size: 48px;
        line-height: 80px;
    }
</style>



