<template>
    <div class="section">
        <Head :title="cityServeTitleName" showRight="true" showLeft="true"></Head>
        <ul>
            <li v-for="(c,index) in cityServe[0].list" :key="index">
                <img v-lazy="c.img" alt="">
                <p>{{c.title}}</p>

            </li>
        </ul>
    </div>
</template>



<script>
import store from "../vuex/store";
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    computed:{
        ...mapState([
             'cityServe',
             'cityServeTitleName'
         ]),
    },
    methods:{
        ...mapActions(['getCityServe'])
    },
    created(){
        var url = window.location.href.split("?")[1]
        var msg = decodeURI(url.split('=')[1])
        this.getCityServe({url:'vue/cityServe',msg});
    }
}
</script>


<style lang="scss" scoped>
    .section{
        margin-top: 80px;
    }
    ul{
        width: 100%;    
        li{
            width: 100%;
            margin-bottom: 4px;
            position: relative;
            img{
                width: 100%;
                height: 240px;
            }
            p{
                position: absolute;
                left: 0;
                bottom: 20px;
                width: 80%;
                padding-left: 30px;
                box-sizing: border-box;
                font-size: 32px;
                color:#fff;
            }
        }
    }
</style>



