<template>
    <div class="section">
        <Head :title="titleName" showRight="true" showLeft="true"></Head>
        <mt-loadmore :top-method="loadTop" ref="loadmore">
        <ul>
            <li v-for="(l,index) in list[0].list" :key="index" @click="gotoDetail(list[0].d.categ,l.id)">
                <h3>{{l.name}}</h3>
                <p>{{l.tels}}</p>
                <div class="site clearfix">
                    <div class="left">{{l.address}}</div>
                    <div class="right">{{l.distance}}</div>
                </div>
            </li>
        </ul>
        </mt-loadmore>
    </div>
</template>


<script>
import store from "../vuex/store";
import { Toast } from "mint-ui"
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    computed:{
        ...mapState([
             'list',
             'titleName'
         ]),
    },
    methods:{
        ...mapActions(['getList']),
        gotoDetail(key,id){
            this.$router.push('/detail?key='+this.list[0].d.categ+'&id='+id+'&main='+this.titleName)
        },
        loadTop(){
            console.log('下拉刷新')
            setTimeout(()=>{
                Toast('下拉刷新数据成功!');
                window.location.reload()
                    this.$refs.loadmore.onTopLoaded();
            },1500)
        }
     },
     created(){
        var url = window.location.href.split("?")[1]
        var key = decodeURI(url.split('&')[0].split("=")[1])
        var titleName = decodeURI(url.split('&')[1].split("=")[1])
        this.getList({url:'vue/serve',key,titleName});
     }
}
</script>



<style lang="scss" scoped>
    .section{
        padding-top: 80px;
    }
    ul{
        width: 100%;
        background-color: #fff;
        li{
            width: 100%;
            padding: 18px;
            box-sizing: border-box;
            border-bottom: 2px solid #e0e0e0;
            h3{
                font-size: 36px;
            }
            p{
                font-size: 26px;
                line-height: 50px;
                color:#f19411;
            }
            .site{
                width: 100%;
                font-size: 26px;
                color:#999;
                .left{
                    float: left;
                    width: 80%;
                    text-align: left;
                }
                .right{
                    float: right;
                    width: 20%;
                    text-align: right;
                }
            }
        }
        
    }
</style>




