<template>

    <div class="section">
        <Head :title="'周边服务'" showRight="true"></Head>
        <mt-loadmore :top-method="loadTop" ref="loadmore">
            <div class="collect clearfix">
                <p>我的收藏</p>
                <i class="iconfont" v-html="'&#xe64e;'"></i>
            </div>
            <h3>
                热门分类
            </h3>
            <ul>
                <li v-for="(h,index) in hot" :key="index" @click="gotoList(h.pid+','+h.id,h.name)">{{h.name}}</li>
            </ul>
            <h3>
                {{homemaking.name}}
            </h3>
            <ul>
                <li v-for="(n,index) in homemaking.node" :key="index" @click="gotoList(n.pid+','+n.id,n.name)">{{n.name}}</li>
            </ul>
            <h3>
                {{cate.name}}
            </h3>
            <ul>
                <li v-for="(c,index) in cate.node" :key="index" @click="gotoList(c.pid+','+c.id,c.name)">{{c.name}}</li>
            </ul>
            <h3>
                {{recreation.name}}
            </h3>
            <ul>
                <li v-for="(r,index) in recreation.node" :key="index" @click="gotoList(r.pid+','+r.id,r.name)">{{r.name}}</li>
            </ul>
            <h3>
                {{hotel.name}}
            </h3>
            <ul>
                <li v-for="(h,index) in hotel.node" :key="index" @click="gotoList(h.pid+','+h.id,h.name)">{{h.name}}</li>
            </ul>
            <h3>
                {{hairdressing.name}}
            </h3>
            <ul>
                <li v-for="(ha,index) in hairdressing.node" :key="index" @click="gotoList(ha.pid+','+ha.id,ha.name)">{{ha.name}}</li>
            </ul>
            <h3>
                {{car.name}}
            </h3>
            <ul>
                <li v-for="(ca,index) in car.node" :key="index" @click="gotoList(ca.pid+','+ca.id,ca.name)">{{ca.name}}</li>
            </ul>
            <h3>
                {{shopping.name}}
            </h3>
            <ul>
                <li v-for="(s,index) in shopping.node" :key="index" @click="gotoList(s.pid+','+s.id,s.name)">{{s.name}}</li>
            </ul>
        </mt-loadmore>
    </div>
    
</template>

<script>
import store from "../vuex/store";
import { Toast } from "mint-ui"
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    computed:{
         ...mapState([
             'service',
             'hot',
             'homemaking',
             'cate',
             'recreation',
             'hotel',
             'hairdressing',
             'car',
             'shopping'
         ]),
         service:{
            get(){
                return this.$store.state.service;
            }
        },
        hot:{
            get(){
                return this.$store.state.hot;
            }
        },
        homemaking:{
            get(){
                return this.$store.state.homemaking;
            }
        },
        cate:{
            get(){
                return this.$store.state.cate;
            }
        },
        recreation:{
            get(){
                return this.$store.state.recreation;
            }
        },
        hotel:{
            get(){
                return this.$store.state.hotel;
            }
        },
        hairdressing:{
            get(){
                return this.$store.state.hairdressing;
            }
        },
        car:{
            get(){
                return this.$store.state.car;
            }
        },
        shopping:{
            get(){
                return this.$store.state.shopping;
            }
        },
    },
     methods:{
        ...mapActions(['getService','getList']),
        gotoList(key,titleName){
            this.$router.push('/list?key='+key+'&titleName='+titleName)
        },
        loadTop(){
            console.log('下拉刷新')
            setTimeout(()=>{
                Toast('下拉刷新数据成功!');
                window.location.reload()
                    this.$refs.loadmore.onTopLoaded();
            },1500)
        }
     },
     mounted(){  
        this.getService({url:'vue/service'});
       
    }
}
</script>


<style lang="scss" scoped>
    .section{
        padding-top: 80px;
        padding-bottom: 90px;
    }
    .collect{
        width: 100%;
        padding: 0 30px;
        box-sizing: border-box;
        margin-top: 30px;
        background-color: #fff;
        border-top: 2px solid #e0e0e0;
        border-bottom: 2px solid #e0e0e0;
        p{
            font-size: 32px;
            line-height: 80px;
            color:#ff6600;
            float: left;
        }
        i{
            display: block;
            font-size: 32px;
            line-height: 80px;
            float: right;
        }
    }
    h3{
        width: 100%;
        padding:20px 30px;
        font-size: 32px;
        margin-top: 16px;
        box-sizing: border-box;
        color:#165cb2;
    }
    ul{
        width: 100%;
        background-color: #fff;
        display: flex;
        justify-content:flex-start;
        flex-wrap:wrap;
        li{
            width: 33.33%;
            height: 90px;
            line-height: 90px;
            box-sizing: border-box;
            text-align: center;
            font-size: 32px;
            border:1px solid #e0e0e0;
        }
    }
</style>



