<template>
    <div class="section">
        <Head :title="'更多'"></Head>
        <ul class="nav">
            <li @click="username?gotoUserinfo():gotologin()">
                <i v-html="'&#xe659;'" class="iconfont account"></i>
                <span>账号管理</span>
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
            </li>
            <li>
                <i v-html="'&#xe601;'" class="iconfont us"></i>
                <span>关于我们</span>
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
            </li>
            <li>
                <i v-html="'&#xe62b;'" class="iconfont scan"></i>
                <span>扫一扫</span>
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
            </li>
            <li>
                <i v-html="'&#xe632;'" class="iconfont eliminate"></i>
                <span>清除缓存</span>
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
            </li>
            <li>
                <i v-html="'&#xe61a;'" class="iconfont privacy"></i>
                <span>用户协议和隐私政策</span>
                <i class="iconfont path" v-html="'&#xe64e;'"></i>
            </li>
        </ul>
    </div>
</template>


<script>
import store from "../vuex/store";
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    methods:{
        gotologin(){
            this.$router.push({name:"login"})
        },
        gotoUserinfo(){
            this.$router.push({name:"userinfo"})
        }
    },
    computed:{
        ...mapState(['username']),
        username:{
             get(){
                 return localStorage.username
             }
         }
    }
}
</script>


<style lang="scss" scoped>
    .section{
        padding-top: 80px;
    }
    .nav{
    width: 100%;
    border-top:2px solid #b2b2b2;
    li{
        width: 100%;
        height: 100px;
        box-sizing: border-box;
        border-bottom:2px solid #ddd;
        padding:20px;
        font-size: 30px;
        background-color: #fff;
        i{
            display: inline-block;
            vertical-align: middle;
            text-align: center;
            font-size: 45px;
            color:#929292;
            line-height: 60px;
            border-radius: 50%;
            width: 60px;
            height: 60px;
        }
        span{
            padding-left: 10px;
            display: inline-block;
            vertical-align: middle;
            line-height: 60px;
        }
        .path{
            float:right;
            text-align: center;
            width: 60px;
            height: 60px;
            line-height: 60px;
            color: #c5c5c5;
        }
    }
}
</style>




