


import Vue from "vue";
import VueRouter from "vue-router";

// 0
Vue.use(VueRouter)

// 1. 注册路由组件
import Layout from "./views/layout.vue";
import Guide from "./views/guide.vue"
import Home from "./views/home.vue";
import List from "./views/list.vue";
import Service from "./views/service.vue";
import More from "./views/more.vue";
import Mine from "./views/mine.vue";
import Detail from "./views/detail.vue";
import Login from "./views/login.vue";
import Register from "./views/register.vue";
import CityServer from "./views/cityserver.vue";
import SplList from "./views/splList.vue";
import Userinfo from "./views/userinfo.vue";
import ChangePwd from "./views/changePwd.vue";
import GoldWeal from "./views/goldWeal.vue";
import Post from "./views/post.vue";
import SetPost from "./views/setPost.vue";


// 2. 配置路由选项 
const routes = [
    {
        path:"/",
        component:Guide,
        name:"guide",
    },
    {
        path:"/app",
        component:Layout,
        name:"app",
        children:[
            {path:"",component:Home},
            {path:"home",component:Home,name:"home"},
            {path:"service",component:Service,name:"service"},
            {path:"more",component:More,name:"more"},
            {path:"mine",component:Mine,name:"mine"},
            {path:"*",redirect:"/app/home"}
        ]
    },
    {path:"/list",component:List,name:"list"},
    {path:"/cityServe",component:CityServer,name:"cityServe"},
    {path:"/splList",component:SplList,name:"splList"},
    {path:"/userinfo",component:Userinfo,name:"userinfo"},
    {path:"/changePwd",component:ChangePwd,name:"changePwd"},
    {path:"/goldWeal",component:GoldWeal,name:"goldWeal"},
    {path:"/post",component:Post,name:"post"},
    {path:"/setPost",component:SetPost,name:"setPost"},
    {
        path:"/detail",
        name:"detail",
        component:Detail
    },
    {
        path:"/login",
        name:"login",
        component:Login
    },
    {
        path:"/register",
        name:"register",
        component:Register
    },
    {
        path:"*",
        redirect:"/app/home"
    }
]

// 3. 创建router
const router = new VueRouter({
    routes,
    mode:"hash"
});


export default router;


