<template>
    <div class="section">
        <mt-header :title="'发贴'" >
            <mt-button icon="back" slot="left" @click="goback"></mt-button>
            <mt-button type="primary" size="small" slot="right" @click="setPost">提交</mt-button>
        </mt-header>
        <div class="content">
            <input type="text" placeholder="请填写标题" v-model="postTitle">
            <br>
            <textarea v-model="postContent" placeholder="请填写内容"></textarea>
        </div>
    </div>
</template>


<script>
import axios from "axios";
import store from "../vuex/store";
import { Toast } from 'mint-ui'
import {mapState,mapMutations,mapActions} from "vuex";
export default {
    computed:{
        ...mapState(['postTitle','postContent']),
        postTitle:{
            get(){
                return this.$store.state.postTitle
            },
            set(newval){
                this.changeTitle(newval)
            }
        },
        postContent:{
            get(){
                return this.$store.state.postContent
            },
            set(newval){
                this.changeContent(newval)
            }
        }
    },
    methods:{
        ...mapMutations(['changeTitle','changeContent']),
        setPost(){
            var nick = localStorage.nickname;
            if(this.postTitle != ""&&this.postContent != ""){
                 axios.post('http://118.31.74.14:3080/vue/setPost',
                    {
                        types: "bbs",
                        istop: "0",
                        title: this.postTitle,
                        brief: this.postContent,
                        nick,
                        hot: '0',
                        review: '0',
                        imgs: [],
                        times: new Date().getTime()/1000
                    }
                ).then(res=>{
                    console.log(res.data);
                    Toast(res.data);
                    this.postTitle = "";
                    this.postContent = ""
                    this.$router.go(-1)
                })
            }
        },
        goback(){
            console.log(this.$router);
            this.$router.go(-1);
        }
    }
}
</script>






<style lang="scss" scoped>
    .mint-header{
        width: 100%;
        box-sizing: border-box;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 1000;
        background-color: #f8f8f8;
        color: #4d4d4d;
        font-size: 34px;
        border-bottom: 2px solid #b2b2b2;
    }
    .mint-button--small{
        font-size: 28px;
        display: inline-block;
        padding: 0 20px;
        line-height: 50px;
        border:0;
        background-color:blue;
        color:#fff;
        border-radius: 6px;
    }
    .content{
        padding: 0 20px;
        padding-top: 80px;
        background-color: #fff;
        input{
            width: 100%;
            height: 60px;
            line-height: 60px;
            font-size: 28px;
            border:0;
            border-bottom: 2px solid #ccc;
        }
        textarea{
            width: 100%;
            line-height: 50px;
            height: 300px;
            border:0;
            font-size: 26px;

        }
    }
    
</style>




